.secondSection {
    position: relative;
    width: 100%;
    height: 100vh;
    background-color: black; /* 검은 배경 */
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    background-image: radial-gradient(circle, rgba(0, 0, 40, 0.8) 0%, rgba(0, 0, 0, 1) 100%);
}

/* 별 배경 효과 */
.secondSection::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(circle at 50% 50%, #ffffff 1px, transparent 1px);
    background-size: 3px 3px;
    opacity: 0.5;
    animation: twinkling 3s infinite ease-in-out;
}

@keyframes twinkling {
    0% { opacity: 0.3; }
    50% { opacity: 0.7; }
    100% { opacity: 0.3; }
}

.secondSectionText {
    font-size: 7rem;
    color: rgba(173, 216, 230, 0); /* 글자 초기 상태 */
    animation: textFadeIn 7s ease forwards, textMoveUp 7s ease forwards; /* 글자 페이드인과 위로 이동 애니메이션 */
}

@keyframes textFadeIn {
    0% {
        color: rgba(173, 216, 230, 0);
        text-shadow: 0 0 10px rgba(68, 114, 196, 0.8); /* 푸른 빛 */
    }
    100% {
        color: rgba(173, 216, 230, 1); /* 완전히 나타난 글자 */
        text-shadow: 0 0 30px rgba(68, 114, 196, 0.8),
        0 0 60px rgba(173, 216, 230, 0.8); /* 그림자 */
    }
}

@keyframes textMoveUp {
    0% {
        transform: translateY(100px); /* 아래에서 시작 */
    }
    100% {
        transform: translateY(0); /* 정상 위치로 */
    }
}